import React, { useRef } from 'react'
import { BookOpen, Code, Info, PieChart, Twitter, Send, Rss, GitHub } from 'react-feather'
import { useTranslation } from 'react-i18next'
import styled from 'styled-components'
import { ReactComponent as MenuIcon } from '../../assets/images/menu.svg'
import { useActiveWeb3React } from '../../hooks'
import { useOnClickOutside } from '../../hooks/useOnClickOutside'
import { ApplicationModal } from '../../state/application/actions'
import { useModalOpen, useToggleModal } from '../../state/application/hooks'
import { ExternalLink } from '../../theme'
import { ButtonPrimary } from '../Button'

const StyledMenuIcon = styled(MenuIcon)`
  path {
    stroke: ${({ theme }) => theme.text1};
  }
`

const StyledMenuButton = styled.button`
  width: 100%;
  height: 100%;
  border: none;
  background-color: transparent;
  margin: 0;
  padding: 0;
  height: 35px;
  background-color: ${({ theme }) => theme.bg3};

  padding: 0.15rem 0.5rem;
  border-radius: 0.5rem;

  :hover,
  :focus {
    cursor: pointer;
    outline: none;
    background-color: ${({ theme }) => theme.bg4};
  }

  svg {
    margin-top: 2px;
  }
`

const StyledMenu = styled.div`
  margin-left: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border: none;
  text-align: left;
`

const MenuFlyout = styled.span`
  min-width: 8.125rem;
  background-color: ${({ theme }) => theme.bg3};
  box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.01), 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 16px 24px rgba(0, 0, 0, 0.04),
    0px 24px 32px rgba(0, 0, 0, 0.01);
  border-radius: 12px;
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  font-size: 1rem;
  position: absolute;
  top: 3rem;
  right: 0rem;
  z-index: 100;

  ${({ theme }) => theme.mediaWidth.upToMedium`
    top: -13.25rem;
  `};
`

const MenuItem = styled(ExternalLink)`
  flex: 1;
  padding: 0.5rem 0.5rem;
  color: ${({ theme }) => theme.text2};
  font-size: 0.75rem;
  :hover {
    color: ${({ theme }) => theme.text1};
    cursor: pointer;
    text-decoration: none;
  }
  > svg {
    margin-right: 8px;
    vertical-align: middle;
  }
`

const CODE_LINK = 'https://github.com/Uniswap/uniswap-interface'

export default function Menu() {
  const { t, i18n } = useTranslation()
  const { account } = useActiveWeb3React()
  const hideSomething = true

  const node = useRef<HTMLDivElement>()
  const open = useModalOpen(ApplicationModal.MENU)
  const toggle = useToggleModal(ApplicationModal.MENU)
  useOnClickOutside(node, open ? toggle : undefined)
  const openClaimModal = useToggleModal(ApplicationModal.ADDRESS_CLAIM)
  return (
    // https://github.com/DefinitelyTyped/DefinitelyTyped/issues/30451
    <StyledMenu ref={node as any}>
      <StyledMenuButton onClick={toggle}>
        <StyledMenuIcon />
      </StyledMenuButton>

      {open && (
        <MenuFlyout>
          <MenuItem id="link" href={`/OriSwap_whitepaper_${i18n.language === 'en-US' ? 'EN' : 'CN'}.pdf`}>
            <BookOpen size={14} />
            {t('whitepaper')}
          </MenuItem>
          <MenuItem id="link" href={`/Oriswap_audit_report_${i18n.language === 'en-US' ? 'EN' : 'CN'}.pdf`}>
            <Code size={14} />
            {t('audit')}
          </MenuItem>
          <MenuItem id="link" href="https://info.oriswap.xyz/">
            <PieChart size={14} />
            {t('info')}
          </MenuItem>
          <MenuItem id="link" href="https://t.me/XXX">
            <Send size={14} />
            Telegram
          </MenuItem>
          <MenuItem id="link" href="https://twitter.com/XXXX">
            <Twitter size={14} />
            Twitter
          </MenuItem>
          <MenuItem id="link" href="https://t.me/XXXXX">
            <Rss size={14} />
            Medium
          </MenuItem>
          <MenuItem id="link" href="https://github.com/XXX">
            <GitHub size={14} />
            Github
          </MenuItem>
          {!hideSomething && (
            <>
              <MenuItem id="link" href="https://uniswap.org/">
                <Info size={14} />
                About
              </MenuItem>
              <MenuItem id="link" href="https://uniswap.org/docs/v2">
                <BookOpen size={14} />
                Docs
              </MenuItem>
              <MenuItem id="link" href={CODE_LINK}>
                <Code size={14} />
                Code
              </MenuItem>
              <MenuItem id="link" href="https://discord.gg/XXX">
                {/* <MessageCircle size={14} /> */}
                Discord
              </MenuItem>
              <MenuItem id="link" href="https://uniswap.info/">
                <PieChart size={14} />
                Analytics
              </MenuItem>
              {account && (
                <ButtonPrimary onClick={openClaimModal} padding="8px 16px" width="100%" borderRadius="12px" mt="0.5rem">
                  Claim DOG
                </ButtonPrimary>
              )}
            </>
          )}
        </MenuFlyout>
      )}
    </StyledMenu>
  )
}
